<template>
  <v-app>
    <v-app-bar
        fixed
        color="white"
        elevation="1"
    >

      <!--Back button-->
      <v-app-bar-nav-icon>
        <i class="fa fa-arrow-left fa-2x" style="color:#000000;" aria-hidden="true"></i>
      </v-app-bar-nav-icon>

      <!-- Page title -->
      <v-spacer></v-spacer>
      <v-toolbar-title id="Title">Edit Profile</v-toolbar-title>
      <v-spacer></v-spacer>

    </v-app-bar>
    
    <!--Page-->
    <v-main id="main">
      <v-container>
        <div class="content">
          <!--User Avatar-->
          <div class="avatar_container">
            <v-avatar
                class="elevation-8"
                size="152"
            >
              <img :src="userData.avatar" alt="">
            </v-avatar>
          </div>

          <image-input v-model="avatar">
            <div slot="activator">
              <v-avatar size="150px" v-ripple v-if="!avatar" class="grey lighten-3 mb-3">
                <span>Click to add avatar</span>
              </v-avatar>
              <v-avatar size="150px" v-ripple v-else class="mb-3">
                <img :src="avatar.imageURL" alt="avatar">
              </v-avatar>
            </div>
          </image-input>
          <v-slide-x-transition>
            <div v-if="avatar && saved == false">
              <v-btn class="primary" @click="uploadImage" :loading="saving">Save Avatar</v-btn>
            </div>
          </v-slide-x-transition>

      </v-container>
    </v-main>
  </v-app>
</template>

<script>
import ImageInput from '../../components/ImageInput';
import axios from 'axios'
export default {
  name: 'App',

  components: {
    ImageInput: ImageInput
  },

  data: () => ({
    userData: null,
    avatar: null,
    saving: false,
    saved: false
  }),

  watch:{
    avatar: {
      handler: function() {
        this.saved = false
      },
      deep: true
    }
  },

  mounted() {
    axios.get('/public/profile/getUserData').then(response => (this.userData = response["data"]))
    // axios.get('/public/profile/getTags').then(response => (this.tags = response["data"]))
    // axios.get('/public/profile/getBadges').then(response => (this.badges = response["data"]))
  },

  methods: {
    uploadImage() {
      this.saving = true
      setTimeout(() => this.savedAvatar(), 1000)
    },
    savedAvatar() {
      this.saving = false
      this.saved = true
    }
  }
};
</script>

<style>
@import '../styles.css';
#main {
  background-image: url(leaves.png);
  background-color: rgba(255, 255, 255, 0.4);
  background-blend-mode: lighten;
  background-repeat: repeat;
  background-position: center;
  
}

.content{
  margin-top: 3.5rem;
  display: flex;
  align-content: center;
}


</style>